<template>
    <div class="video">
      <!-- <router-link to="/"> -->
      <img class="closeButton" src="../../../assets/CombinedShape.png" alt="" @click="closeButton()" >
      <!-- </router-link> -->
      <video id="my-video" controls width="250" webkit-playsinline="true" playsinline="true" x5-playsinline >
        <source :src="$route.query.myid" type="video/mp4">
        Sorry, your browser doesn't support embedded videos.
      </video>
      <div class="prompt" v-show="promptTypr">
        <p>当前非WIFI环境，播放视频会消耗数据流量，确定播放？</p>
        <div class="pause" @click="control(0)">暂停播放</div>
        <div class="play" @click="control(1)">继续播放</div>
      </div>
    </div>
</template>
<script>
export default {
  name: 'HomeVideo',
  data(){
    return {
      controla: '',
      promptTypr: true
    };
  },
  methods: {
    closeButton(){
      alert('a');
      this.$router.push({ path: "/" });
    },
    control(e){
      this.promptTypr = false
      if(e == 1){
        this.controla.play()
      }
    },
    ifPrompt(val){
      if(val){
        if(val != 'none'){
          if(val == 'wifi'){
            this.controla.play()
            console.log("wifi")
            this.promptTypr = false
          }else {
            this.controla.pause()
            console.log("no wifi")
            this.promptTypr = true
          }
        }
      }
    }
  },
  mounted () {
    if (!window.navigator) {
      alert("No navigator!");
    }
    this.controla = document.getElementById('my-video')
    // var a = navigator.connection;
    var connection = navigator.connection || navigator.mozConnection || navigator.webkitConnection;
    alert(connection.type)
    this.ifPrompt(connection.type)
    connection.onchange = (e)=>{
      alert(e.currentTarget.type)
      this.ifPrompt(e.currentTarget.type)
    }
  }
}
</script>

<style scoped>
.closeButton{
  width: 6vw;
  position: absolute;
  top: 15vw;
  left: 4vw;
}
.video{
  width: 100vw;
  height: 100vh;
  background-color: #000;
}
#my-video{
  width: 100vw;
  height: 40vh;
  margin-top: 27vh;
}
.prompt{
  width: 100vw;
  height: 40vh;
  margin-top: 27vh;
  position: absolute;
  top: 0;
  color: #fff;
  background-color: rgb(0, 0, 0,0.5)
}
.prompt p{
  width: 82vw;
  margin: 20vw auto 0;
  text-align: center;
  line-height: 8vw;
  font-size: 4vw;
} 
.prompt div{
  width: 30vw;
  line-height: 10vw;
  text-align: center;
  font-size: 4vw;
  float: left;
  margin-left: 13.222vw;
  margin-top: 4vw;
  border-radius: 0.5vw;
}
.prompt .pause{
  border: 1px solid #fff;
}
.prompt .play{
  border: 1px solid #ba6954;
  background-color: #ba6954;
}
.prompt .play:hover{
  border: 1px solid #b17a6c;
  background-color: #b17a6c;
}
video::-webkit-media-controls-overlay-play-button {
  /* background-image:url(../../../assets/playbutton.png);
  background-color: rgb(0, 0, 0, 0); */
  /* display: none; */
}
/* input[pseudo="-webkit-media-controls-overlay-play-button" i] div{
  display: none;
} */

</style>



