import Vue from 'vue'
import Router from 'vue-router'
import HomeHeader from '@/pages/home/components/Header'
import Video from '@/pages/home/components/Video.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomeHeader',
      component: HomeHeader
    },{
      path: '/Video',
      name: 'Video',
      component: Video
    }
  ]
})



