<template>
    <div class="img">
        <div class="button4" @click="button(1)"></div>
        <div class="button5" @click="button(2)"></div>
        <div class="button6" @click="button(3)"></div>
    </div>
</template>

<script>
var u = navigator.userAgent;
var isAndroid = u.indexOf("Android") > -1 || u.indexOf("Linux") > -1; //g
var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
export default {
  name: 'HomeHeader',
  methods: {
    button(e){
      if(e==1){
        this.$router.push({ path: "/Video?myid=https://media.byton.cn/20181217/BYT_UIUX_FHD_v04_CN-PRESS-OC_v03.mp4" });
      }else if(e==2){
        this.$router.push({ path: "/Video?myid=https://media.byton.cn/20181217/BYT_BRST_TECH_SAFETY_EN_v01_H264_40MBit.mp4" });
      }else{
        if (isAndroid) {
          Android.toBrandStore(JSON.stringify({ id: '1127858930945388545' }))
        }
        if (isIOS) {
          window.webkit.messageHandlers.getBrandStoreId.postMessage({id: '1127858930945388545'});
        }
      }
    }
  }
}
</script>

<style scoped>
    .img{
        overflow:hidden;
        width:100%;
        height:0;
        padding-bottom:1440.8%;
        background-image:url('../../../assets/carshow.jpg');
        background-size:cover;
        position:relative;
    }
    .footer{
        width:100%;
        height:1.3%;
        position:absolute;
        display:flex;
        bottom:0;
    }
    .button1{
        height:100%;
        width:33.3%;
    }
    .button2{
        height:100%;
        width:33.3%;
        background-color:red;
    }
    .button3{
        height:100%;
        width:33.3%;
        background-color:red;
    }
    .button4{
        width:14.408%;
        height:1%;
        position:absolute;
        top:43.6%;
        left:43%;
    }
    .button5{
        width:14.408%;
        height:1%;
        position:absolute;
        bottom:30.8%;
        left:43%;
    }
    .button6{
        width:30.5%;
        height:0.8%;
        position:absolute;
        bottom:6.1%;
        left:35%;
    }
</style>



